import BASE_HOST from '../../util/util.js'
export default {
	data() {
		return {
			isSearch: false,
			showModel: '',
			newOptionText: '',
			hotList: [],
			newList: [],
			pageSize: 5,
			pageNum: 1,
			totalNum: 0,
			totalTopic: 0,
			matchStr: '',
			searchList: [],
			USERID: 0
		}
	},
	filters: {
		dateFormat(value) {
			var d = new Date(value)
			return (d.getMonth() + 1) + '-' + d.getDate()
		}
	},
	onShow() {
		this.getHotTopic()
		this.getNewTopic()
		this.getTotalTopic()
		this.USERID = uni.getStorageSync('local_userinfo').token
	},
	watch: {
		'matchStr': function(val) {
			if(val === '') {
				this.isSearch = false
			}
		}
	},
	methods: {
		toDetail(item) {
			uni.navigateTo({
				url:'./forum_detail?id=' + item.id
			})
		},
		deleteTopic(id) {
			var _this = this
			uni.request({
				url: BASE_HOST + 'topic/delete',
				method: 'GET',
				data: {
					id: id
				},
				success(res) {
					uni.showToast({
					    title: '删除成功!',
					    duration: 2000
					});
					_this.getHotTopic()
					_this.getNewTopic()
					_this.getTotalTopic()
					_this.searchBlur()
				}
			})
		},
		getNewTopic() {
			var _this = this
			uni.request({
				url: BASE_HOST + 'topic/getNewTopic',
				method: 'GET',
				data: {
					pageSize: 3,
				},
				success(res) {
					_this.newList = res.data.data
					_this.newList.forEach((item, index)=> {
						if(item.user_avatar.substr(0, 5) === 'https') {
							item.user_avatar = 'background-image:url(' + item.user_avatar + ');'
						} else {
							item.user_avatar = 'background-image:url(' + BASE_HOST + item.user_avatar + ');'
						}
					})
				}
			})
		},
		getHotTopic() {
			var _this = this
			uni.request({
				url: BASE_HOST + 'topic/getHotTopic',
				method: 'GET',
				data: {
					pageSize: 6,
				},
				success(res) {
					_this.hotList = res.data.data
					_this.hotList.forEach((item, index)=> {
						if(item.user_avatar.substr(0, 5) === 'https') {
							item.user_avatar = 'background-image:url(' + item.user_avatar + ');'
						} else {
							item.user_avatar = 'background-image:url(' + BASE_HOST + item.user_avatar + ');'
						}
					})
				}
			})
		},
		enshureCreateOption() {
			if(this.newOptionText === '') {
				uni.showToast({
					title: '请填写话题！',
					duration: 2000,
					icon: 'none'
				})
				return
			}
			var _this = this
			uni.request({
				url: BASE_HOST + 'topic/add',
				method: 'POST',
				data: {
					userId: uni.getStorageSync('local_userinfo').token,
					topicText: _this.newOptionText
				},
				success(res) {
					console.log(res.data)
					if(res.data.code === 20000) {
						_this.showModel = ''
						uni.showToast({
							title: '新增成功',
							duration: 2000
						})
					}
				}
			})
		},
		toCreateOption() {
			this.showModel = 'show'
		},
		hideModal() {
			this.showModel = ''
		},
		searchBlur() {
			this.isSearch = true
			if(this.matchStr === '') {
				return
			}
			var _this = this
			uni.request({
				url:BASE_HOST + 'topic/search',
				method:'GET',
				data:{
					matchStr: _this.matchStr
				},
				success(res) {
					_this.searchList = res.data.data
					if (_this.searchList.length === 0) {
						uni.showToast({
						    title: '未搜索到相关信息',
						    duration: 2000,
								icon:'none'
						});
						_this.isSearch = false
						return
					}
					_this.searchList.forEach((item, index)=> {
						if(item.user_avatar.substr(0, 5) === 'https') {
							item.user_avatar = 'background-image:url(' + item.user_avatar + ');'
						} else {
							item.user_avatar = 'background-image:url(' + BASE_HOST + item.user_avatar + ');'
						}
					})
				}
			})
		},
		getTotalTopic(){
			var _this = this
			uni.request({
				url:BASE_HOST + 'topic/count',
				method:'GET',
				success(res) {
					_this.totalTopic = res.data.data.totalTopic
				}
			})
		}
	}
}
